form{
  diplay: inline-block;
}

.BackdropColor {
  background: -webkit-gradient(linear,  left top, left bottom,  from(#f27a5e),color-stop(74%, #f25050),color-stop(95%, #f25050));
  background: -webkit-linear-gradient(top,  #f27a5e 0%,#f25050 74%,#f25050 95%);
  background: linear-gradient(to bottom,  #f27a5e 0%,#f25050 74%,#f25050 95%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f27a5e', endColorstr='#f25050',GradientType=0 ); /* IE6-9 */
  height: 100vw;
}


.postIt{
  box-shadow: 6px 0px 19px -1px rgba(0,0,0,0.38);
  background-color: white;
  display: inline-block;
  vertical-align: top;
  padding: 10px;
  /* transition: transform .2s; */
}

