.nameInput{
  width: 100%;
  height: 40px;
  width: 100%;
  height: 110px;
  border: none;
  text-align: center;
  font-size: 50px;
  font-family: 'Permanent Marker', cursive;
  color: #010440git ;
  background-color: #F2F0CE;
}

.gridme{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

#nameBox {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
  background-color: #F2F0CE;
}
#fillMurrayBox {
  -webkit-transform: rotate(-1deg);
          transform: rotate(-1deg);
}
#placeCageBox {
  -webkit-transform: rotate(-2.3deg);
          transform: rotate(-2.3deg);
}
#customHeaderBox {
  display: inline-block;
  text-align: center;
  -webkit-transform: rotate(4deg);
          transform: rotate(4deg);
  padding: 31px;
}
#customBackgroundBox {
    padding: 31px;
    display: inline-block;
    text-align: center;
    -webkit-transform: rotate(-0.5deg);
            transform: rotate(-0.5deg);
}
#awwSnapBox {
  display: inline-block;
  text-align: center;
  -webkit-transform: rotate(4deg);
          transform: rotate(4deg);
  background-image: url("https://systweak1.vo.llnwd.net/content/wp/systweakblogsnew/uploads_new/2018/03/How-to-Fix-Aw-Snap-Error-in-Chrome1.jpg");
  background-size: 90%;
  padding: 69px;
}

#submitButtonBox {
  display: inline-block;
  text-align: center;
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
  padding: 37px;
  background-image: url("https://visualpharm.com/assets/258/Save-595b40b65ba036ed117d160b.svg");
  background-size: 51%
}


input[type=checkbox], input[type=radio] {
    height: 2em;
    width: 4em;
}

.padding10px {
  padding: 10px;
  display: inline-block;
}

#submitButton {
  height: 36px;
  width: 74px;
  margin: 7px 0px;
  border-radius: 5px;
}
#submitButton:hover {
  background-color: green;
  color: white;
  height: 36px;
  width: 74px;
  margin: 7px 0px;
  border-radius: 5px;
}

